}
.app-wrapper {
  background-color: #f8f8f8;
  width: 100%;
  height: 100%;
}
.app-container {
  width: 100%;
  height: 100%;
}
.filter {
  -moz-filter: blur(3px);
  -ms-filter: blur(3px);
